.header {
  background-color: transparent;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  color: white;
  text-align: right;
  background-color: var(--react-color);
  height: 90px;
  font-size: 1.4em;
  font-weight: 500;
  letter-spacing: 1px;
}

.header .div-logo {
}

.header .div-logo .logo {
  width: 100px;
  height: 50px;
  color: var(--react-text-color);
  margin-left: 30px;
}

.header .div-logo .logo:hover {
  opacity: 0.5;
}

.header .navigator {
  flex-grow: 1;
  margin-right: 100px;
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
}

.header .hamburguer {
  display: none;
}

.header .navigator .anchor {
  text-decoration: none;
  padding: 0 30px;
  color: white;
}

.header .navigator .anchor:hover {
  color: var(--react-text-color);
}

.header .bright {
  opacity: var(--opacity);
  cursor: pointer;
  border-radius: 30px;
  background-color: var(--bright-btn);
  box-shadow: var(--shadow);
}

.header button {
  background-color: transparent;
  color: white;
  border: none;
  font-size: 1em;
  margin-top: -7px;
}

@media screen and (max-width: 1250px) {
  .header {
    font-size: 1.1em;
    font-weight: 400;
  }
  .header .navigator {
    margin-right: 20px;
  }
}

@media screen and (max-width: 1081px) {
  .header {
    font-weight: 400;
  }
  .header .navigator {
    margin-right: 20px;
  }
}

@media screen and (max-width: 965px) {
  .header {
    font-size: 0.8em;
    font-weight: 400;
    justify-content: space-between;
  }
  .header .div-logo .logo {
    margin-left: 10px;
    padding-left: 0;
    width: 80px;
    height: 40px;
  }
  .header .navigator {
    overflow: hidden;
    height: 0;
    transition: all 0.5s ease;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    position: absolute;
    top: 90px;
    width: 100vw;
    background-color: var(--react-color);
    line-height: 50px;
  }
  .header .active {
    height: 50vh;
    pointer-events: auto;
    z-index: 1;
    font-size: 1.5em;
    margin-right: 0;
  }

  .header .hamburguer {
    display: inline;
    margin-right: 40px;
    font-size: 2.3em;
    cursor: pointer;
  }
  .header .hamburguer:hover {
    opacity: 0.6;
  }
}

@media screen and (max-width: 400px) {
  .header .hamburguer {
    margin-right: 25px;
  }
}
